import { css } from '../../base/src/index';
const styles = css`
    .grid-container {
        display: grid;
        grid-template-columns: 40px 50px auto 50px 40px;
        grid-template-rows: auto auto auto;
        grid-template-columns: 1fr 1fr;
        grid-gap: 0 3rem;
    }
`;
export default styles;